export default {

	data() {
		return {
			titleNViewBackground: '',
			swiperCurrent: 0,
			carouselList: [],
			goodsList: []
		};
	},

	onLoad() {
		this.loadData();
	},
	computed: {
		swiperLength() {
			return this.carouselList.length
		}
	},
	methods: {
		/**
		 * 请求静态数据只是为了代码不那么乱
		 * 分次请求未作整合
		 */
		async loadData() {
			uni.showLoading({
				title: '数据加载中123...'
			})

			var {
				error_code,
				data
			} = await this.$http.indexIndex({
				haha: 1234
			})

			if (error_code === 0) {
				uni.hideLoading()
				this.carouselList = data.home_banner
			}

			console.log(data);
			console.log(error_code);
		},
		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
			this.titleNViewBackground = this.carouselList[index].background;
		},
		//详情页
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.title;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
	},

























}
